import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import {
  Row,
  Col,
  Image,
  ListGroup,
  Button,
  Card,
  Form,
} from "react-bootstrap";
import Rating from "../components/Rating";
import Loader from "../components/Loader";
import Message from "../components/Message";
import { useDispatch, useSelector } from "react-redux";
import {
  listProductDetails,
  createProductReview,
} from "../actions/productActions";
import { PRODUCT_CREATE_REVIEW_RESET } from "../constans/productConstants";

function ProductScreen({ match, history }) {
  const [qty, setQty] = useState(1);
  const [rating, setRating] = useState(0);
  const [comment, setComment] = useState("");

  const dispatch = useDispatch();
  const productDetail = useSelector((state) => state.productDetails);
  const { loading, error, product } = productDetail;

  const userLogin = useSelector((state) => state.userLogin);
  const { userInfo } = userLogin;

  const productReviewCreate = useSelector((state) => state.productReviewCreate);
  const {
    loading: loadingProductReview,
    error: errorProductReview,
    success: successProductReview,
  } = productReviewCreate;

  const submitHandler = (e) => {
    e.preventDefault()
    dispatch(createProductReview(match.params.id, {
      rating, comment
    }))
  }

  // let product = {}
  useEffect(() => {

    if (successProductReview ) {
      setRating(0)
      setComment("")
      dispatch({type:PRODUCT_CREATE_REVIEW_RESET})
    }
    dispatch(listProductDetails(match.params.id));
  }, [dispatch, match, successProductReview]);

  const addToCartHandler = () => {
    history.push(`/cart/${match.params.id}?qty=${qty}`);
  };
  return (
    <div>
      <Link to="/" className="btn btn-outline-dark my-3">
        Go Back
      </Link>

      {loading ? (
        <Loader />
      ) : error ? (
        <Message>{error}</Message>
      ) : (
        <Row>
          <Col md={6}>
            <Image src={product.image} alt={`${product.name} Image`} fluid />
          </Col>
          <Col md={3}>
            <ListGroup variant="flush">
              <ListGroup.Item>
                <h3>{product.name}</h3>
              </ListGroup.Item>

              <ListGroup.Item>
                <Rating
                  value={product.rating}
                  text={`${product.numReviews} Reviews`}
                  color={"#f8e825"}
                />
              </ListGroup.Item>

              <ListGroup.Item>{product.description}</ListGroup.Item>
            </ListGroup>
          </Col>
          <Col md={3}>
            <Card>
              <ListGroup variant="flush">
                <ListGroup.Item>
                  <Row>
                    <Col>Price:</Col>

                    <Col>
                      <strong>$ {product.price}</strong>
                    </Col>
                  </Row>
                </ListGroup.Item>

                <ListGroup.Item>
                  <Row>
                    <Col>Status:</Col>

                    <Col>
                      {product.countInStock > 0
                        ? `${product.countInStock} In Stock`
                        : "Out of stock"}
                    </Col>
                  </Row>
                </ListGroup.Item>

                <ListGroup.Item>
                  <Row>
                    {product.countInStock > 0 && (
                      <ListGroup.Item className="my-2">
                        <Row>
                          <Col>Qty</Col>
                          <Col xs="auto">
                            <Form.Control
                              as="select"
                              value={qty}
                              onChange={(e) => setQty(e.target.value)}
                            >
                              {[...Array(product.countInStock).keys()].map(
                                (x) => (
                                  <option value={x + 1} key={x + 1}>
                                    {x + 1}
                                  </option>
                                )
                              )}
                            </Form.Control>
                          </Col>
                        </Row>
                      </ListGroup.Item>
                    )}
                    <Button
                      onClick={addToCartHandler}
                      className="btn-block btn-dark"
                      disabled={product.countInStock < 1}
                      type="Button"
                    >
                      Add to Cart
                    </Button>
                  </Row>
                </ListGroup.Item>
              </ListGroup>
            </Card>
          </Col>
        </Row>
      )}

      <Row>
        <Col md={6}>
          <h4 className="my-5">Reviews</h4>
          {product && product.reviews && product.reviews.length == 0 && <Message variant="info">This product doesn't have any Reviews yet.</Message>}
          <ListGroup variant="flush">
            {product && product.reviews && product.reviews.map((r) => (
              <ListGroup.Item key = {r._id}>
                <strong>{r.name}</strong>
                <Rating 
                value = {r.rating}
                  color={"#f8e825"}
                  />
                <p>{r.createdAt.substring(0,10)}</p>
                <p>{r.comment}</p>
              </ListGroup.Item>
            ))}

            <ListGroup.Item>
              <h4>Write a Review</h4>
              {loadingProductReview && <Loader/>}
              {successProductReview && <Message variant = "success">Review succesfully created</Message>}
              {errorProductReview && <Message variant = "danger">{errorProductReview}</Message>}
              {userInfo ? (
                <Form onSubmit={submitHandler}>
                  <Form.Group controlId = "rating">
                    <Form.Label>
                      Rating
                    </Form.Label>
                    <Form.Control
                     as="select"
                     value = {rating}
                     onChange = {(e) => setRating(e.target.value)}
                     
                     >
                       <option value = "">Select...</option>
                       <option value = "1">1 - Poor</option>
                       <option value = "2">2 - Fair</option>
                       <option value = "3">3 - Good</option>
                       <option value = "4">4 - Very Good</option>
                       <option value = "5">5 - Excelent</option>


                    </Form.Control>
                  </Form.Group>

                  <Form.Group controlId = "comment">
                    <Form.Label>Review</Form.Label>
                    <Form.Control
                     as="textarea"
                      row="5"
                      value={comment}
                      onChange={(e) => setComment(e.target.value)}
                    
                    ></Form.Control>
                  </Form.Group>
                  <Button
                  disabled = {loadingProductReview}
                  type = "submit"
                  variant = "primary"
                  >Create Review
                    
                  </Button>
                </Form>
              ): (
                <Message variant="info">Please <Link to= "/login">Login</Link> to write a Review</Message>
              )}
            </ListGroup.Item>

          </ListGroup>
        </Col>
      </Row>
    </div>
  );
}

export default ProductScreen;
